<template>
	<div class="body fundManage_body">
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height="320">
					<el-table-column prop="index" label="序号" width="100" fixed>
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{(currentPage-1)*pageSize + scope.$index }}
							</div>
							<div class="" v-else>
								新增
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="fund_type" label="投资标的类别" width="150">
						<template slot-scope="scope">
							<!-- 编辑行 -->
							<div class="" v-if="scope.row.id">
								{{ scope.row.fund_type_name }}
							</div>
							<!-- 新增行 -->
							<div class="" v-else>
								<el-select v-model="invest_type" @change="investTypeChange" clearable placeholder="请选择">
								    <el-option v-for="item in add_info" :key="item.fund_type" :label="item.fund_type_name" :value="item.fund_type">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="shortname" label="投资标的简称" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-select v-model="edit_fund_id" filterable clearable placeholder="请选择">
									    <el-option v-for="item in invest_name_list_edit" :key="item.id" :label="item.name" :value="item.id">
									    </el-option>
									</el-select>
								</div>
								<div class="" v-else>
									{{ scope.row.shortname }}
								</div>
							</div>
							<div class="" v-else>
								<el-select v-model="fund_id" filterable clearable placeholder="请选择">
								    <el-option v-for="item in invest_name_list" :key="item.id" :label="item.name" :value="item.id">
								    </el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="protocol_date" label="协议签署日" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-date-picker v-model="edit_protocol_date" type="date" :editable='false'  value-format="yyyy-MM-dd" placeholder="选择日期">
    									</el-date-picker>
								</div>
								<div class="" v-else>
									{{ scope.row.protocol_date }}
								</div>
							</div>
							<div class="" v-else>
								<el-date-picker v-model="protocol_date" type="date" :editable='false' value-format="yyyy-MM-dd" placeholder="选择日期">
    								</el-date-picker>
							</div>
						</template>
					</el-table-column>
					<!-- <el-table-column prop="invest_end_date" label="投资到期日" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<el-date-picker v-model="edit_invest_end_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
    									</el-date-picker>
								</div>
								<div class="" v-else>
									{{ scope.row.invest_end_date }}
								</div>
							</div>
							<div class="" v-else>
								<el-date-picker v-model="invest_end_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
    								</el-date-picker>
							</div>
						</template>
					</el-table-column> -->
					<el-table-column prop="invest_end_date" label="投资到期日" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.invest_end_date?scope.row.invest_end_date:'-' }}
							</div>
							<div v-else>
								{{ scope.row.invest_end_date}}
							</div>
							
						</template>
					</el-table-column>
					
					
					<el-table-column prop="invest_scale" label="投资占比" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="flex_between" v-if="scope.row.id == edit_id">
									<div class="flex_between" v-if="scope.row.fund_type == 3">
										<el-input placeholder="请输入内容" v-model="edit_invest_scale" clearable>
										</el-input>
										%
									</div>
									<div class="" v-else>
										{{ scope.row.invest_scale }}&nbsp;%
									</div>
								</div>
								<div class="" v-else>
									{{ scope.row.invest_scale }}&nbsp;%
								</div>
							</div>
							<div class="" v-else>
								<div class="flex_between" v-if="invest_type == 3">
									<el-input placeholder="请输入内容" v-model="invest_scale" clearable>
									</el-input>
									%
								</div>
								<div class="" v-else>
									
								</div>
							</div>
						</template>
					</el-table-column>
					
					
					<el-table-column prop="subscribe" label="认缴（万元）" width="140">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id == edit_id">
									<div class="" v-if="scope.row.fund_type == 2 || scope.row.fund_type == 3">
										<el-input placeholder="请输入内容" v-model="edit_subscribe" clearable>
										</el-input>
									</div>
									<div class="" v-else>
										{{ scope.row.subscribe }}
									</div>
								</div>
								<div class="" v-else>
									{{ scope.row.subscribe }}
								</div>
							</div>
							<!--新增样式-->
							<div class="" v-else>
								<div class="" v-if="invest_type == 2 || invest_type == 3">
									<el-input placeholder="请输入内容" v-model="subscribe" clearable>
									</el-input>
								</div>
								<div class="" v-else>
									
								</div>
							</div>
						</template>
					</el-table-column>
					
					<el-table-column prop="address" label="币种" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.unit == 1?'人民币':'美元' }}
								<!-- <div class="" v-if="scope.row.id == edit_id">
									<div class="" v-if="scope.row.fund_type == 2 || scope.row.fund_type == 3">
										<el-select v-model="edit_unit" clearable placeholder="请选择">
										    <el-option v-for="item in coin_list" :key="item.value" :label="item.label" :value="item.value">
										    </el-option>
										</el-select>
									</div>
									<div class="" v-else>
										{{ scope.row.unit == 1?'人民币':'美元' }}
									</div>
								</div>
								<div class="" v-else>
									{{ scope.row.unit == 1?'人民币':'美元' }}
								</div> -->
							</div>
							<!--新增样式-->
							<div class="" v-else>
								<!-- <div class="" v-if="invest_type == 2 || invest_type == 3">
									<el-select v-model="coin" clearable placeholder="请选择">
									    <el-option v-for="item in coin_list" :key="item.value" :label="item.label" :value="item.value">
									    </el-option>
									</el-select>
								</div>
								<div class="" v-else>
									
								</div> -->
								<div class=""></div>
							</div>
						</template>
					</el-table-column>
					
					
					
					<el-table-column prop="is_check" label="是否确权" width="140">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.is_check === 1">
								是
							</div>
							<div class="" v-if="scope.row.is_check === 0">
								否
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="multiple" label="倍数" width="120">
					</el-table-column>
					<el-table-column prop="update_time" label="更新时间" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.update_time?scope.row.update_time:'-' }}
							</div>
							<div v-else>
								{{ scope.row.update_time}}
							</div>
							
						</template>
					</el-table-column>
					<el-table-column prop="investment" label="投资（万元）" width="120">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="150">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="180">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.row.id != edit_id">
									<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
									<el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
									<el-button type="text" size="small" @click="deleteInvestGroup(scope.row)">删除</el-button>
								</div>
								<div class="" v-else>
									<el-button type="text" size="small" @click="editSave(scope.row)">保存</el-button>
									<el-button type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
								</div>
							</div>
							<div class="" v-else>
								<el-button type="text" size="small" @click="addMotherFundInvestGroup(scope)">保存</el-button>
								<el-button type="text" size="small" @click="clearData()">清除</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
				<el-pagination v-if="totalCount" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="totalCount">
    				</el-pagination>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getMotherFundInvestGroupListApi,getMotherFundInvestGroupAddInfoApi,addMotherFundInvestGroupApi,editMotherFundInvestGroupApi,deleteMotherFundInvestGroupApi,getSelfFundInvestGroupListApi,getSelfFundInvestGroupAddInfoApi,addSelfFundInvestGroupApi,editSelfFundInvestGroupApi,deleteSelfFundInvestGroupApi } from '../../config/api'

	export default {
		inject: ['reload'],
		
		name: 'motherFundRunInvestGroup',
		data() {
			return {
				id: '',
				type: 1,//1代表母基金；2代表自管基金
				currentPage: 1,
				pageSize: 10,
				totalCount: 0,
				addData: {
					id: 0,
					unit: 3,
				},
				add_info: [],
				invest_name_list: [],
				invest_name_list_edit: [],
				//添加新数据的字段开始
				invest_type: '',
				fund_id: '',
				subscribe: 0,
				invest_scale: 0,
				coin: '',
				protocol_date: '',
				invest_end_date: '',
				// 添加新数据的字段结束
				//编辑保存接口字段开始
				edit_id: '',
				edit_fund_id: '',
				edit_unit: '',
				edit_fund_type: '',
				edit_subscribe: '',
				edit_invest_scale:'',
				edit_protocol_date: '',
				edit_invest_end_date: '',
				//编辑保存接口字段结束
				coin_list: [
					{
						label: '人民币',
						value: 1
					},
					{
						label: '美元',
						value: 2
					}
				],
				
				tableData: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.getMotherFundInvestGroupList()
			this.getMotherFundInvestGroupAddInfo()
		},
		mounted() {},
		components: {},
		methods: {
			getMotherFundInvestGroupList(){
				if (this.type == 1) {
					getMotherFundInvestGroupListApi({
						fof_id: this.id,
						page: this.currentPage,
						size: this.pageSize
					}).then(res => {
						if(res.data.msgNo == 1) {
							let data = res.data.payload;
							this.tableData = data.data;
							this.totalCount = data.count;
							this.tableData.unshift(this.addData);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					getSelfFundInvestGroupListApi({
						pet_fund_id: this.id,
						page: this.currentPage,
						size: this.pageSize
					}).then(res => {
						//console.log(res);
						if(res.data.msgNo == 1) {
							this.tableData = res.data.payload.data;
							this.totalCount = res.data.payload.count;
							this.tableData.unshift(this.addData);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			getMotherFundInvestGroupAddInfo(){
				if (this.type == 1) {
					getMotherFundInvestGroupAddInfoApi({
						fof_id: this.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.add_info = res.data.payload.data;
						} else {
							// dialogConfirm("info", res.data.message);
						}
					})
				} else{
					getSelfFundInvestGroupAddInfoApi({
						pet_fund_id: this.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.add_info = res.data.payload.data;
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			addMotherFundInvestGroup(){
				// &&this.invest_end_date
				// console.log( this.coin);
				// return false;
				if(this.fund_id&&this.invest_type&&this.protocol_date){
					if (this.invest_type == 3) {
						if ( Number(this.invest_scale) < 0 || Number(this.invest_scale) > 100 || Number(this.invest_scale) != this.invest_scale) {
							dialogConfirm("info", '投资占比录入错误');
							return
						} 
					}
					if (this.type == 1) {
						addMotherFundInvestGroupApi({
							fof_id: this.id,
							fund_id: this.fund_id,
							// unit: this.coin,
							unit:'',
							fund_type: this.invest_type,
							subscribe: this.subscribe,
							invest_scale:this.invest_scale,
							protocol_date: this.protocol_date,
							// invest_end_date: this.invest_end_date
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.invest_type = '';
								this.fund_id = '';
								this.coin = '';
								this.protocol_date = '';
								// this.invest_end_date = '';
								this.subscribe = '';
								this.invest_scale = '',
								this.getMotherFundInvestGroupList()
								this.getMotherFundInvestGroupAddInfo()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						addSelfFundInvestGroupApi({
							pet_fund_id: this.id,
							fund_id: this.fund_id,
							// unit: this.coin,
							unit:'',
							fund_type: this.invest_type,
							subscribe: this.subscribe,
							invest_scale:this.invest_scale,
							protocol_date: this.protocol_date,
							// invest_end_date: this.invest_end_date
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.invest_type = '';
								this.fund_id = '';
								this.coin = '';
								this.protocol_date = '';
								// this.invest_end_date = '';
								this.subscribe = '';
								this.invest_scale = '',
								this.getMotherFundInvestGroupList()
								this.getMotherFundInvestGroupAddInfo()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}
				}else{
					dialogConfirm("info", '不能存在空值!');
				}
				
			},
			investTypeChange(e){
				this.fund_id = '';
				this.coin = 1;
				if(e){
					this.invest_name_list = this.add_info[e-1].data;
				}else{
					this.invest_name_list = [];
				}
			},
			investTypeChangeEdit(e){
				// console.log(e);
				this.edit_fund_id = '';
				if(e){
					this.invest_name_list_edit = this.add_info[e-1].data;
				}else{
					this.invest_name_list = [];
				}
			},
			edit(e){
				// console.log(e)
				this.invest_name_list_edit = this.add_info[e.fund_type - 1].data;
				this.edit_id = e.id;
				this.edit_fund_type = parseInt(e.fund_type);
				this.edit_fund_id = parseInt(e.fund_id);
				this.edit_unit = e.unit;
				this.edit_subscribe = e.subscribe;
				this.edit_invest_scale = e.invest_scale,
				this.edit_protocol_date = e.protocol_date;
				// this.edit_invest_end_date = e.invest_end_date;
			},
			editSave(e){
				//&&this.edit_invest_end_date
				// console.log(e);
				// return false;
				if(this.edit_fund_id&&this.edit_fund_type&&this.edit_protocol_date){
					if (this.edit_fund_type == 3) {
						if ( Number(this.edit_invest_scale) < 0 || Number(this.edit_invest_scale) > 100 || Number(this.edit_invest_scale) != this.edit_invest_scale) {
							dialogConfirm("info", '投资占比录入错误');
							return
						} 
					}
					if (this.type == 1) {
						editMotherFundInvestGroupApi({
							fof_id: this.id,
							fund_id: this.edit_fund_id,
							// unit: this.edit_unit,
							unit: e.unit,
							fund_type: this.edit_fund_type,
							subscribe: this.edit_subscribe,
							invest_scale:this.edit_invest_scale,
							protocol_date: this.edit_protocol_date,
							// invest_end_date: this.edit_invest_end_date,
							id: this.edit_id
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.edit_id = -1;
								this.getMotherFundInvestGroupList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						editSelfFundInvestGroupApi({
							pet_fund_id: this.id,
							fund_id: this.edit_fund_id,
							// unit: this.edit_unit,
							unit: e.unit,
							fund_type: this.edit_fund_type,
							subscribe: this.edit_subscribe,
							invest_scale:this.edit_invest_scale,
							protocol_date: this.edit_protocol_date,
							// invest_end_date: this.edit_invest_end_date,
							id: this.edit_id
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.edit_id = -1;
								this.getMotherFundInvestGroupList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}
				}else{
					dialogConfirm("info", '不能存在空值!');
				}
			},
			editCancel(){
				this.edit_id = -1;
			},
			// 判断删除的时候是否是最后一条记录
			isLeftOneItem(){
				let length = this.tableData.length;
				if(length == 2 && this.currentPage > 1){
					this.currentPage = this.currentPage - 1;
				}
			},
			deleteInvestGroup(e) {
				var id = e.id;
				dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
					if(this.type == 1){
						deleteMotherFundInvestGroupApi({
							id: id
						}).then(res => {
							//console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.isLeftOneItem()
								this.getMotherFundInvestGroupList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else {
						deleteSelfFundInvestGroupApi({
							id: id
						}).then(res => {
							//console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.isLeftOneItem()
								this.getMotherFundInvestGroupList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}
				}).catch(() => {});
			},
			clearData(){
				this.invest_type = '';
				this.fund_id = '';
				this.subscribe = '';
				this.invest_scale = '';
				this.coin = '';
				this.protocol_date = '';
				this.invest_end_date = '';
			},
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getMotherFundInvestGroupList()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getMotherFundInvestGroupList()
			},
			view(e) {
				var type = e.fund_type;
				var component = '';
				if (type == 1) {
					component = "motherFundCompanyDetail";
				} else if(type == 2){
					component = "subFundDetail";
				} else if(type == 3){
					component = "directInvestProjectView";
				} else {
					component = "selfAdministerFundCompanyDetail";
				}
				this.$router.push({
					name: component,
					query: {
						id: e.fund_id
					}
				})
			}

		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px !important;
	}
	
</style>


